<template>
    <div>
        <nav class="navbar has-shadow is-spaced is-fixed-top" role="navigation" aria-label="main navigation">
            <div class="container">
                <div class="navbar-brand">
                    <a class="navbar-item" href="https://bulma.io">
                    <img src="https://bulma.io/images/bulma-logo.png" alt="Bulma: a modern CSS framework based on Flexbox" width="112" height="28">
                    </a>

                    <a role="button" class="navbar-burger" data-target="navMenu" aria-label="menu" aria-expanded="false">
                    <span aria-hidden="true"></span>
                    <span aria-hidden="true"></span>
                    <span aria-hidden="true"></span>
                    </a>
                </div>

                <div class="navbar-menu" id="navMenu">

                    <div class="navbar-start">
                        <a class="navbar-item" v-smooth-scroll="{ duration: 1000, offset: -93 }" href="#aboutMe">
                            <span class="icon has-text-primary">
                                <i class="fas fa-info" aria-hidden="true"></i>
                            </span>
                            <span>About Me</span>
                        </a>

                        <a class="navbar-item" v-smooth-scroll="{ duration: 1000, offset: -93 }" href="#project">
                            <span class="icon has-text-primary">
                                <i class="fas fa-passport" aria-hidden="true"></i>
                            </span>
                            <span>Project</span>
                        </a>

                        <a class="navbar-item" v-smooth-scroll="{ duration: 1000, offset: -400 }" href="#socialMedia">
                            <span class="icon has-text-primary">
                                <i class="fab fa-facebook" aria-hidden="true"></i>
                            </span>
                            <span>Social Media</span>
                        </a>

                    </div>

                    <div class="navbar-end">
                        <div class="navbar-item">
                            <p class="control">
                                <a class="button is-primary is-outlined is-rounded is-medium" v-smooth-scroll="{ duration: 1000, offset: -93 }" href="#contactMe">
                                    <span>Contact Me!</span>
                                </a>
                            </p>
                        </div>
                    </div>
                </div>

            </div>
            
        </nav>
    </div>
</template>

<script>

    document.addEventListener('DOMContentLoaded', () => {

    // Get all "navbar-burger" elements
    const $navbarBurgers = Array.prototype.slice.call(document.querySelectorAll('.navbar-burger'), 0);

    // Check if there are any navbar burgers
    if ($navbarBurgers.length > 0) {

        // Add a click event on each of them
        $navbarBurgers.forEach( el => {
        el.addEventListener('click', () => {

            // Get the target from the "data-target" attribute
            const target = el.dataset.target;
            const $target = document.getElementById(target);

            // Toggle the "is-active" class on both the "navbar-burger" and the "navbar-menu"
            el.classList.toggle('is-active');
            $target.classList.toggle('is-active');

        });
        });
    }

    });

    export default {

    }
</script>